.elearing_blog {
	padding: 5rem 0;
}
.elearing_blog .blogs_artcl {
	font-size: 24px;
	color: #1b75bb;
	font-family: "Poppins-Medium";
	display: -webkit-box;
	max-width: 100%;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis;
}

.elearing_blog .read_more {
	color: #1b75bb;
	font-size: 20px;
	font-family: "Poppins-Medium";
}
.elearing_blog .card-eblogs {
	border: 1px solid lightgray;
	border-radius: 15px;
}

.elearing_blog .crd-inner {
	padding: 2rem 1.5rem;
}
