.Auth_page .bg_auth_img {
	background-image: url("../Images/Auth/classmates-friends-bag-school-education.png");
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	min-height: 100vh;
}

.Auth_page {
	background-image: url("../Images/Auth/Auth_bg.png");
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	min-height: 50vh;
}

.Auth_page .fild-div {
	display: flex;
	flex-direction: column;
	height: 100%;
	justify-content: center;
	padding-right: 1.5rem;
}

.Auth_page .main-head {
	font-size: 40px;
	font-family: "Poppins-Bold";
	/* font-family: "Poppins-Bold"; */
}

.Auth_page .future_text {
	font-family: "Poppins-Bold";
	color: #f4812a;
	font-size: 24px;
}

.Auth_page .welcm-txt {
	font-family: "Poppins-Regular";
	font-size: 24px;
	margin-bottom: 0;
}

.Auth_page .inpt-fld {
	border-radius: 200px;
	border: 1px solid #d4d4d4;
	background: transparent;
	padding: 0.7rem 1.5rem;
	margin-top: 1rem;
	font-family: "Poppins-medium";
	color: black;
}

.Auth_page .inpt-fld:focus {
	outline: none;
}

.Auth_page .bt-auth {
	background-image: linear-gradient(180deg, #1b75bb, #0e3b5e);
	color: white;
	font-size: 12px;
	padding: 0.7rem 1.5rem;
	border-radius: 200px;
	font-family: "Poppins-Regular";
}

.Auth_page .remeber_me {
	font-size: 14px;
}

.Auth_page .forgot_pass {
	font-size: 14px;
	cursor: pointer;
}

.Auth_page .no_acc {
	font-size: 13px;
	color: #636060;
	font-family: "Poppins-Light";
}

.Auth_page .regstr_now {
	font-size: 13px;
	color: #1b75bb;
	cursor: pointer;
	font-family: "Poppins-regular";
}

.Auth_page .pb-enter-email {
	padding-bottom: 15rem;
}

.Auth_page .best_place {
	font-size: 20px;
	font-family: "Poppins-Regular";
}

@media only screen and (max-width: 991px) {
	.Auth_page .main-head {
		font-size: 22px;
		margin-top: 1rem;
	}

	.Auth_page .welcm-txt {
		font-size: 14px;
	}

	.Auth_page .best_place {
		font-size: 12px;
	}
	.Auth_page .future_text {
		font-size: 13px;
	}
	.Auth_page .bg_auth_img {
		display: none;
	}
	.Auth_page .no_acc {
		font-size: 9px;
	}
	.Auth_page .regstr_now {
		font-size: 9px;
		margin-left: 0 !important;
	}
	.Auth_page .fild-div {
		padding-right: 0;
		padding-bottom: 1rem;
		min-height: 100vh;
	}
}
