.header .header_full .header_ul {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 0;
	margin: 0px;
	background-color: transparent;
}

.header .brdr-btm-actv {
	width: 100%;
	border: 3px solid #17639f;
	border-radius: 20px;
	position: absolute;
}

.header .underline {
	border-bottom: 4px solid #17639f;
}

.header .w-85 {
	width: 80%;
}

.header .text-dec-link {
	text-decoration: none;
}

.header {
	background-color: transparent;
	font-size: 18px;
	color: #2d477c;
	cursor: pointer;
}

.header_full {
	padding: 1rem 0 0 0;
}

.header .logo_header {
	height: 120px;
}

.header .header_ul li {
	color: black;
	text-decoration: none;
	list-style: none;
	font-size: 13px;
	/* padding-bottom: 0.5rem; */
	font-family: "Poppins-Medium";
}

.header .active::before {
	content: "\2022";
	color: red;
	font-weight: bold;
	display: inline-block;
	width: 0.6em;
	margin-left: -1em;
	font-size: 20px;
}

.header .profile_img {
	width: 35px;
	height: 35px;
	align-self: center;
	position: relative;
	top: 0px;
}

.header .inpt-fld {
	border-radius: 50px;
	border: 0.5px solid #1b75bb;
	padding: 0.8rem;
	padding-left: 2rem;
	width: 170px;
	font-size: 13px;
	margin-left: 0.6rem;
}

.header .inpt-fld:focus {
	outline: none;
}

.header .srch_icon {
	position: absolute;
	left: 25%;
	top: 37%;
	color: black;
	font-size: 16px;
}

.header-small {
	display: none;
}

@media only screen and (max-width: 1300px) {
	.header .header_ul li {
		font-size: 10px;
	}
}

@media only screen and (max-width: 992px) {
	.header-small {
		display: block;
	}

	.header {
		display: none;
	}

	.HeaderBanner {
		margin-top: 0rem !important;
	}
}

@media only screen and (max-width: 1200px) {
	.header .inpt-fld {
		width: 140px;
	}

	.header .srch_icon {
		left: 25%;
	}

	.header .profile_img {
		height: 25px;
		width: 25px;
	}

	.header .inpt-fld {
		padding: 0.6rem;
		padding-left: 2rem;
	}
}

/* header banner starts*/
.HeaderBanner {
	background-image: linear-gradient(180deg, #1b75bb, #0e3b5e);
	padding-top: 2rem;
	color: white;
	margin-top: 1rem;
}

.HeaderBanner .w-fire {
	width: 30px;
	position: relative;
	top: -10px;
}

.HeaderBanner .title {
	font-size: 36px;
	font-family: "Poppins-Bold";
	margin-left: 2rem;
}

.HeaderBanner .align-self-cntr {
	align-self: center;
}

.HeaderBanner .header-banner-childrenimg {
	width: 100%;
	position: relative;
	top: 1px;
}

@media only screen and (max-width: 992px) {
	.HeaderBanner .title {
		font-size: 22px;
		margin-left: 1rem;
	}

	.HeaderBanner .w-fire {
		width: 20px;
		top: -5px;
	}
}

/* // second header for responsive starts*/

.header-small {
	display: none;
}

.swip_header_sml {
	display: none;
}

@media only screen and (max-width: 992px) {
	.bg-red-res-hdr {
		background-color: #fff;
		color: black;
		margin-top: 2px;
		margin-right: 6px;
	}

	.header-full {
		display: none;
	}

	.swip_header_sml {
		display: block;
	}

	.header-small {
		display: block;
	}

	.header .flx-header-box {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}

	.header-small .img_respon {
		height: 40px;
		padding: 5px 5px !important;
		background-color: #fff !important;
		/* width: 30px; */
	}

	.header-small .line-small-hdr {
		margin-left: 10px;
	}

	.header-small .bg-red-hdr {
		background-color: #1b75bb;
		color: white;
		display: flex;
		justify-content: space-between;
		padding: 0.5rem 0.5rem;
	}

	.header-small .align-center {
		align-self: center;
	}

	.clr-hom-sml-hdr {
		color: #1b75bb;
		text-decoration: none;
	}

	.clr-hom-sml-hdr :hover {
		color: #202076 !important;
		text-decoration: none;
		/* border-bottom: 1px solid #df1921; */
		/* padding-bottom: 0.5rem; */
	}

	.list-style-type-circle {
		list-style-type: circle;
	}

	.flx-1 {
		flex: 1;
	}

	.flx-2 {
		flex: 3.5;
	}

	.flex-end-hdr-smll {
		justify-content: flex-end;
		cursor: pointer;
		color: #df1921;
	}

	.flex-end-hdr-smll:hover {
		color: #202076;
	}

	.close_btn_sml_hdr {
		float: right;
		margin-right: 1rem;
	}

	.MuiDrawer-root > .MuiPaper-root {
		border-radius: 0px 0px 8px 8px;
	}

	.swip_header_sml .css-1ytz6dp {
		background-color: #1b75bb;
	}

	.pos-rel-top-four {
		position: relative;
		top: 3px;
		left: 12px;
		font-size: 12px;
	}

	.content_control_swiphead_res_header {
		width: 100vw;
		height: 100%;
		padding: 10px 30px;
	}
}

/* // second header for responsive ends*/

/* header banner ends here */
