.Accredition{
    padding-top: 3rem;
    padding-bottom: 8rem;
}

.Accredition .Acc_girl{
    width: 100%;
}

.Accredition .flx-lft-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    width: 100%;
}



.Accredition .select-grades{
    width: 100%;
    padding: 1.7rem;
    padding-left: 2.5rem;
    border-radius: 50px;
    color: #9F9F9F;
    border: 0;
    width: 90%;
    font-family: 'Poppins-Regular';
    box-shadow: 0px 0px 13px #0000001c;
}

.Accredition .select-grades {
    -webkit-appearance: none;
    -moz-appearance: none;
    background: transparent;
    background-image: url("../Images/Accredition/down_arrow.png");
    background-repeat: no-repeat;
    background-position-x: 92%;
    background-position-y: 55%;
}

.Accredition .select-grades:focus{
   outline: none;
}

.Accredition .mt-Acc{
    margin-top: 5rem;
}