.Program .card-progrm {
	border-radius: 20px;
	background-image: linear-gradient(180deg, #1b75bb, #0e3b5e);
	padding: 0rem;
	color: white;
	overflow: hidden;
}

.content-wrapper {
	padding-right: 20px;
}

.Program .bg-img-card {
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	height: 100%;
	width: 100%;
	border-radius: 20px 0px 0px 0;
}

.Program .brdr-btm-lght {
	border-bottom: 1px solid #dcdcdc;
	width: 95%;
}

.Program .w-age {
	width: 50px;
}

.Program .Science_Club {
	font-size: 16px;
	font-family: "Poppins-Bold";
	min-height: 100px;
}

@media only screen and (max-width: 992px) {
	.Program .bg-img-card {
		min-height: 150px;
		width: 100%;
	}
	.Program .pdn-lft {
		padding-left: 3rem;
	}
}
