.footer {
    /* background-image: url('../images/footer-bg.png'); */
    background-image: url('../Images/HeaderFooter/footer_bgimg.png');
    background-position: left;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    padding-top: 4rem;
    padding-bottom: 1rem;
}

.footer .no-dec {
    text-decoration: none !important;
}

.footer .main_heading {
    font-size: 32px;
    font-family: "Poppins-Bold";
}

.footer .lorem {
    font-size: 13px;
    font-family: "Poppins-Regular";
    line-height: 02;
}

.footer .text {
    font-size: 13px;
    font-family: "Poppins-Regular";
}

.footer .head {
    font-size: 20px;
    font-family: "Poppins-Regular";
    margin-bottom: 2rem;
}

.footer .ico {
    width: 50px;
}

.footer .brdr-btm-lightwht {
    border-bottom: 1px solid #ffffff4a;
    width: 95%;
    margin-top: 0.3rem;
}

.footer .pl-rez-ftr {
    display: inline-block;
    text-align: left;
}

.footer .allrights {
    font-size: 12px;
    font-family: "Poppins-Medium";
    margin-top: 2rem;
    margin-bottom: 0;
}

/* footer responsive */



/* oursubscribe starts */
.OurSubscibe .subscribe_inpt {
    width: 100%;
    border-radius: 40px;
    padding: 1.7rem;
    margin-top: 2rem;
    border: 0;
    box-shadow: 0px 0px 13px #0000001c;
    padding-left: 1.5rem;
    font-family: 'Poppins-Medium';
}

.OurSubscibe .subscribe_inpt:focus {
    outline: none;
}

.OurSubscibe .subscribe_div {
    width: 75%;
    margin: auto;
    position: relative;
}

.OurSubscibe .susbcribe-bt {
    position: absolute;
    display: block;
    top: 35px;
    right: 5px;
    padding: 1.4rem 3.5rem;
}

.OurSubscibe .subscribe_section {
    margin-top: 7rem;
    margin-bottom: 5rem;
}

.OurSubscibe .new_platform {
    color: #F4812A;
    font-family: "Poppins-SemiBold";
    font-size: 22px;
    letter-spacing: 2px;
}

.OurSubscibe .trending_courses {
    position: relative;
    top: 13px;
    font-size: 36px;
    font-family: "Poppins-Bold";
    margin-left: 1rem;
}

.OurSubscibe .text-home {
    font-size: 16px;
    font-family: "Poppins-Regular";
    color: #0000006e;
    line-height: 2;
}

@media only screen and (max-width: 991px) {
    .footer .d-flx-ico {
        margin: 2rem 0;
    }

    .OurSubscibe .subscribe_div {
        width: 100%;
    }

    .OurSubscibe .new_platform {
        font-size: 12px;
    }

    .OurSubscibe .text-home {
        font-size: 12px;
    }

    .OurSubscibe .trending_courses {
        font-size: 18px;
    }

    .OurSubscibe .susbcribe-bt {
        font-size: small;
        padding: 0.5rem 2rem;
        top: 42px;
    }
}

@media only screen and (max-width: 991px) {
    .OurSubscibe .subscribe_inpt {
        font-size: 10px;
    }

    .OurSubscibe .susbcribe-bt {
        top: 37px;
    }
}

/* oursubscribe ends */