/* font family starts here */

@font-face {
	font-family: "Roboto-Light";
	font-style: normal;
	font-weight: normal;
	src: local("Roboto-Light"), url("../Fonts/Roboto-Light.ttf") format("woff");
}

@font-face {
	font-family: "Roboto-Regular";
	font-style: normal;
	font-weight: normal;
	src: local("Roboto-Regular"),
		url("../Fonts/Roboto-Regular.ttf") format("woff");
}

@font-face {
	font-family: "Roboto-Medium";
	font-style: normal;
	font-weight: normal;
	src: local("Roboto-Medium"), url("../Fonts/Roboto-Medium.ttf") format("woff");
}

@font-face {
	font-family: "Roboto-Bold";
	font-style: normal;
	font-weight: normal;
	src: local("Roboto-Bold"), url("../Fonts/Roboto-Bold.ttf") format("woff");
}

@font-face {
	font-family: "Poppins-Bold";
	font-style: normal;
	font-weight: bold;
	src: local("Poppins-Bold"), url("../Fonts/Poppins-Bold.ttf") format("woff");
}

@font-face {
	font-family: "Poppins-Regular";
	font-style: normal;
	font-weight: normal;
	src: local("Poppins-Regular"),
		url("../Fonts/Poppins-Regular.ttf") format("woff");
}

@font-face {
	font-family: "Poppins-Light";
	font-style: normal;
	font-weight: normal;
	src: local("Poppins-Light"), url("../Fonts/Poppins-Light.ttf") format("woff");
}

@font-face {
	font-family: "Poppins-SemiBold";
	font-style: normal;
	font-weight: normal;
	src: local("Poppins-SemiBold"),
		url("../Fonts/Poppins-SemiBold.ttf") format("woff");
}

@font-face {
	font-family: "Poppins-Medium";
	font-style: normal;
	font-weight: normal;
	src: local("Poppins-Medium"),
		url("../Fonts/Poppins-Medium.ttf") format("woff");
}

/* font family ends here */

/* font family classes starts here */

.f-rob-light {
	font-family: "Roboto-Light", sans-serif !important;
}

.f-rob-reg {
	font-family: "Roboto-Regular", sans-serif !important;
}

.f-rob-med {
	font-family: "Roboto-Medium", sans-serif !important;
}

.f-rob-bold {
	font-family: "Roboto-Bold", sans-serif !important;
}

/* common colors  starts*/

.clr-2d {
	color: #2d477c;
}

.clr-eb {
	color: #eb9421;
}

.clr-01 {
	color: #015f9e;
}

.clr-2d {
	color: #2d477c;
}

/* common colors ends here */

/* line height starts here */

.lh-00 {
	line-height: 0;
}

.lh-01 {
	line-height: 1 !important;
}

.lh-13 {
	line-height: 1.3 !important;
}

.lh-02 {
	line-height: 2;
}

/* line height ends here */

/* font sizes starts here */

.f-40 {
	font-size: 40px;
}

.f-30 {
	font-size: 30px;
}

.f-23 {
	font-size: 23px;
}

.f-20 {
	font-size: 20px;
}

.f-18 {
	font-size: 18px;
}

/* font-sizes ends here */

/* common misc css starts*/
.w-100 {
	width: 100%;
}

.pos-rel {
	position: relative;
}

.pos-static {
	position: static !important;
}

.red_dot_home {
	height: 25px;
	margin-right: 10px;
	position: relative;
	top: -2px;
}

.align-center {
	align-self: center;
}

.orange_headaa {
	font-family: "Poppins-SemiBold";
	font-size: 16px;
	color: #f4812a;
	margin-bottom: 0;
	line-height: 0.7;
}

.black_headaa {
	font-family: "Poppins-Bold";
	font-size: 32px;
}

.website-text {
	font-size: 14px;
	font-family: "Poppins-Regular";
	color: #707070;
	line-height: 2;
	display: -webkit-box;
	max-width: 100%;
	-webkit-line-clamp: 4;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis;
}
.elearing_blog .card-eblogs img {
	height: 250px;
	width: 100%;
	object-fit: cover;
}
.LearnSkills {
	background-image: url("../Images/home/Auth_bg.png");
	background-size: cover;
	background-position: center center;
	background-attachment: fixed;
}

.cursor_pointer {
	cursor: pointer;
}

@media only screen and (max-width: 992px) {
	.black_headaa {
		font-family: "Poppins-Bold";
		font-size: 22px;
	}
	.orange_headaa {
		font-size: 13px;
	}
	.website-text {
		font-size: 12px;
	}
}

/* common misc css ends here */
