.Contact {
    padding-top: 7rem;
}

.Contact .inpt-fld {
    width: 100%;
    background: transparent;
    border: 1px solid #D4D4D4;
    padding: 0.7rem;
}

.Contact .inpt-txt-fld {
    resize: none;
    width: 100%;
    background: transparent;
    border: 1px solid #D4D4D4;
    padding: 0.7rem;
}

.Contact .inpt-txt-fld:focus {
    outline: none;
}


.Contact .inpt-fld:focus {
    outline: none;
}

.Contact .head_p {
    font-size: 21px;
    font-family: "Poppins-Bold";
}

.Contact .numb {
    color: #1B75BB;
    font-family: "Poppins-Regular";
}

@media only screen and (max-width: 991px) {
    .Contact .pl-ress {
        padding-left: 1rem!important;
        padding-top: 2rem;
    }
}