.FutureTraining {
    padding-top: 0;
}

.FutureTraining .beee {
    width: 90px;
    margin-top: 2rem;
}

.FutureTraining .orange_box_dv {
    padding: 3rem;
    background: linear-gradient(180deg, #FF6E00, #D45F05);
    width: 100%;
    border-radius: 20px;
    color: white;
}

.FutureTraining .design_head {
    font-size: 28px;
    font-family: "Poppins-Bold";
}

.FutureTraining .future_child {
    position: absolute;
    width: 130%;
    top: -63%;
    left: -16%;
}

.FutureTraining .mt-future {
    margin-top: 7rem;
}

.FutureTraining .lft35 {
    left: -52%;
    top: -73%;
    width: 146%;
}

/* registration and acceptance */
.FutureTraining .blue-bx {
    background-image: linear-gradient(180deg, #1B75BB, #0E3B5E);
}

.FutureTraining .website-text1{
    font-size: 11px;
    font-family: "Poppins-Regular";
}

.FutureTraining .f1 {
    width: 140%;
    top: -50%;
    left: -11%;
}

.FutureTraining .f2 {
    width: 122%;
    top: -50%;
    left: -11%;
}

.FutureTraining .f3 {
    width: 142%;
    top: -77%;
    left: -38%;
}

.FutureTraining .mt-box {
    margin-top: 5rem;
}

.FutureTraining .flx-brwn {
    display: flex;
    flex-direction: row;
    background: #FCF9F4;
    padding: 6rem 0;
}

.FutureTraining .flx-1 {
    flex: 1;
    position: relative;
}

.FutureTraining .flx-1-mth-day {
    width: 100%;
    position: absolute;
    left: -27%;
    top: -70%;
}

.FutureTraining .flx-2 {
    flex: 2;
    text-align: center;
}

.FutureTraining .flx-3 {
    flex: 1;
    display: flex;
    padding-right: 2rem;
    padding-top: 4rem;
    justify-content: flex-end;
}

.FutureTraining .bee {
    width: 100px;
    height: 25px;
}

.FutureTraining .pos-girl-abs {
    position: relative;
    top: 10%;
}

@media only screen and (max-width: 992px) {
    .FutureTraining .f1 {
        display: none;
    }
    .FutureTraining .flx-3{
        display: none;
    }
    .FutureTraining .flx-1{
        display: none;
    } 
    .FutureTraining .flx-2{
        padding: 0 1.5rem;
    } 
    .FutureTraining .design_head {
        font-size: 18px;
    }

    .FutureTraining .future_child {
        display: none;
    }
}